import { MoreHorizOutlined } from '@mui/icons-material';
import {
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Menu,
  MenuItem,
} from '@mui/material';
import { map, omit } from 'lodash-es';
import React from 'react';
import Popper from '../../builtIn/components/Popper';
import { useState } from 'react';
import FormWidget from './form';
import { useRef } from 'react';
import { useMemo } from 'react';
import { generateShortId } from '../../misc';
import { useShallowMemo } from '../../hooks/common';

const ListWidget = (props) => {
  const { value, onChange, fields, mainField } = props;

  const identifiedValue = useShallowMemo(() => {
    return value.map((i) => {
      if (i.id) {
        return i;
      }
      return { ...i, id: generateShortId() };
    });
  }, [value]);

  const [popperOpen, setPopperOpen] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [expand, setExpand] = useState('');
  const popperAnchorRef = useRef<HTMLElement>(null);
  const menuAnchorRef = useRef<HTMLElement>(null);

  return (
    <>
      <List
        disablePadding
        sx={{
          border: '1px solid var(--border-1)',
          fontSize: '12px',
        }}
      >
        {map(identifiedValue, (item) => {
          return (
            <ListItem
              disableGutters
              disablePadding
              key={item.id}
              secondaryAction={
                <ListItemButton
                  onClick={(e) => {
                    setMenuOpen(item.id);
                    menuAnchorRef.current = e.currentTarget;
                  }}
                  sx={{
                    fontSize: '12px',
                    padding: '2px',
                    borderRadius: '50%',
                    paddingRight: '4px',
                  }}
                >
                  <MoreHorizOutlined fontSize="inherit" />
                </ListItemButton>
              }
            >
              <ListItemButton
                onClick={(e) => {
                  setPopperOpen(true);
                  setExpand(item.id);
                  popperAnchorRef.current = e.currentTarget;
                }}
                sx={{
                  padding: '4px 6px',
                }}
              >
                <ListItemText
                  sx={{
                    m: '0',
                    '&>.MuiTypography-root': {
                      fontSize: '12px',
                    },
                  }}
                >
                  {item[mainField]}
                </ListItemText>
              </ListItemButton>
            </ListItem>
          );
        })}
      </List>
      {expand && (
        <Popper
          title="选项配置"
          open={popperOpen}
          onOpenChange={setPopperOpen}
          anchorEl={popperAnchorRef.current}
          offset={[-200, 0]}
        >
          <FormWidget
            fields={fields}
            key={expand}
            value={identifiedValue.find((item) => item.id === expand)}
            onChange={(option) => {
              const idx = identifiedValue.findIndex(
                (item) => item.id === option.id,
              );
              const newValue = [...identifiedValue];
              newValue.splice(idx, 1, option);
              onChange(newValue);
            }}
          />
        </Popper>
      )}
      <Menu open={!!menuOpen} anchorEl={menuAnchorRef.current}>
        <MenuItem
          sx={{
            fontSize: '12px',
            color: 'red',
          }}
          onClick={() => {
            const newValue = identifiedValue.filter((i) => i.id !== menuOpen);
            onChange(newValue);
            setMenuOpen(false);
          }}
        >
          删除
        </MenuItem>
      </Menu>
    </>
  );
};

export default ListWidget;
